import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import Loading from "../../Loader/Loading";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useLmsDashboardMaster } from "../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import InstituteLmsManagerProfile from "./InstituteLmsManagerProfile";
import InstituteLmsManagerSide from "./InstituteLmsManagerSide";

const LmsAllStaff = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsAllStaff/LmsAllStaff")
);
const Attendence = lazy(() =>
  import("../../../StaffTab/Attendence/Attendence")
);
const LmsModerator = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsModerator/LmsModerator")
);
const LmsAllottment = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsAllottment/LmsAllottment")
);
const LmsSettoffRule = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsSettoffRule/LmsSettoffRule")
);
const LmsHoliday = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsHoliday/LmsHoliday")
);
const LmsLeaveRequest = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsLeaveRequest/LmsLeaveRequest")
);
const LmsLeaveIssued = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsLeaveIssued/LmsLeaveIssued")
);
const LmsLeaveManageCoff = lazy(() =>
  import(
    "../../UserStaffMember/LmsManager/LmsLeaveManageCoff/LmsLeaveManageCoff"
  )
);
const LmsStaffOverview = lazy(() =>
  import("../../UserStaffMember/LmsManager/LmsStaffOverview/LmsStaffOverview")
);
const InstituteLmsTabController = lazy(() =>
  import("./InstituteLmsTabController")
);
const InstituteLmsLeaveTabController = lazy(() =>
  import("./InstituteLmsLeaveTabController")
);

const InstituteLmsManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { lmsDashboardMaster, lmsDashboardMasterLoading } =
    useLmsDashboardMaster({
      data: {
        lmid: getQuery.state?.lmid,
        mid: "",
      },
      skip: !getQuery.state?.lmid,
    });

  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <InstituteLmsManagerSide
          lmsHead={lmsDashboardMaster?.active_staff}
          cover={lmsDashboardMaster?.cover}
        />
        <div>
          <Suspense fallback={<QvipleLoader />}>
            {!searchParams.get("type") ? (
              <InstituteLmsManagerProfile
                tabManage={lmsDashboardMaster?.tab_manage}
              />
            ) : searchParams.get("type") === "staff" ? (
              <LmsAllStaff
                instituteId={lmsDashboardMaster?.institute?._id}
                staffAlias={
                  lmsDashboardMaster?.institute?.alias_pronounciation
                    ?.staff_alias
                }
                isInstitute
              />
            ) : searchParams.get("type") === "attendance" ? (
              <Attendence instituteId={lmsDashboardMaster?.institute?._id} />
            ) : searchParams.get("type") === "moderator" ? (
              <LmsModerator
                lmid={getQuery.state?.lmid}
                instituteId={lmsDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "allottment" ? (
              <LmsAllottment
                lmid={getQuery.state?.lmid}
                instituteId={lmsDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "setoff" ? (
              <LmsSettoffRule
                lmid={getQuery.state?.lmid}
                instituteId={lmsDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "holiday" ? (
              <LmsHoliday
                lmid={getQuery.state?.lmid}
                instituteId={lmsDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "request" ? (
              <LmsLeaveRequest
                lmid={getQuery.state?.lmid}
                instituteId={lmsDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "issued" ? (
              <LmsLeaveIssued
                lmid={getQuery.state?.lmid}
                instituteId={lmsDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "coff" ? (
              <LmsLeaveManageCoff
                lmid={getQuery.state?.lmid}
                instituteId={lmsDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "staff-overview" ? (
              <LmsStaffOverview />
            ) : searchParams.get("type") === "tab" ? (
              <InstituteLmsTabController lmid={getQuery.state?.lmid} />
            ) : searchParams.get("type") === "leave-type" ? (
              <InstituteLmsLeaveTabController lmid={getQuery.state?.lmid} />
            ) : null}
          </Suspense>
          {lmsDashboardMasterLoading && <Loading />}
        </div>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteLmsManager;
