import { useTranslation } from "react-i18next";
import { assestsUserLmsUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import InstituteSideBg from "../Config/InstituteSideBg";
import InstituteSideCard from "../Config/InstituteSideCard";
import { Suspense, lazy, memo, useState } from "react";
import QvipleLoader from "../../Loader/QvipleLoader";
const UpdateInstituteLmsManager = lazy(() =>
  import("./UpdateInstituteLmsManager")
);

const InstituteLmsManagerSide = ({ lmsHead, cover }) => {
  const { t } = useTranslation();
  const [openMenu, setOpenMenu] = useState(false);

  const onOpenMenu = () => {
    setOpenMenu((pre) => !pre);
  };

  // console.info("how many tmes");
  return (
    <>
      <div>
        <GoBack
          customStyle={{
            width: "100%",
            marginTop: "0",
          }}
        />
        <InstituteSideBg
          avatar={
            cover
              ? `${imageShowUrl}/${cover}`
              : `${assestsUserLmsUrl}/cover.svg`
          }
          onAction={onOpenMenu}
        />
        <InstituteSideCard
          title={`${lmsHead?.staffFirstName || ""} ${
            lmsHead?.staffMiddleName ? lmsHead?.staffMiddleName : ""
          } ${lmsHead?.staffLastName || ""}`}
          subtitle={t("lms_manager")}
          customStyleContainer={{
            borderRadius: "18px 18px 0px 0px",
            border: "none",
            marginTop: "-1.49vw",
          }}
        />
      </div>
      {openMenu && (
        <Suspense fallback={<QvipleLoader />}>
          <UpdateInstituteLmsManager lmsHead={lmsHead} onClose={onOpenMenu} />
        </Suspense>
      )}
    </>
  );
};

export default memo(InstituteLmsManagerSide);
